import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import './index.css';
import { BrowserRouter as HashRouter } from 'react-router-dom';

ReactDOM.render(
    <React.StrictMode>
        <HashRouter basename={process.env.REACT_APP_PUBLIC_URL}>
            <App />
        </HashRouter>
    </React.StrictMode>,
    document.getElementById('root')
);
